body {
    background-color: #85CFF9;
}

#todolist {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 100%;
    max-width: 600px;

    padding: 2rem 1.5rem;
    margin: 0 auto;

    background-color: #fff;
    border-radius: 20px;

    @media (max-width:768px) {
        max-width: unset;
        width: calc(100% - 32px);
    }

}

#todoform {

    display: flex;
    justify-content: space-between;
    align-items: center;

    input {
        border: 1px solid #3579df;
        border-radius: 5px;
        width: calc(80% - 5px);
        padding: 0 12px;
        line-height: 2.25rem;
        height: 2.25rem;

        font-family: 'Noto Sans KR', sans-serif;
    }

    input:focus {
        outline: none;
    }

    button {
        width: calc(20% - 5px);
        line-height: 2.25rem;
        height: 2.25rem;
        appearance: none;
        border: none;
        outline: none;
        background-color: #3579df;
        border-radius: 5px;

        cursor: pointer;

        font-family: 'Noto Sans KR', sans-serif;
        font-size: 1rem;
        color: #fff;
    }
}

#todo {
    margin-top: 2rem;

    .todolist_inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .todolist_inner:not(:first-child) {
        margin-top: 0.875rem;
    }

    .complete {
        font-size: 1.5rem;
        color: #3579df;
        cursor: pointer;
    }
}